import React, {Component} from 'react';
import PropTypes from 'prop-types';

//import '../App.css'
import './style.css';

const btnDelete = {
    fontSize: '12px',
    background: '#ea2027',
    color: '#fff',
    border: 'none',
    padding: '5px 10px',
    borderRadius: '50%',
    cursor: 'pointer',
    marginLeft: '1%'
}


class Task extends Component {

    StyleCompleted() {
        return {
            fontSize: '20px',
            color: this.props.done ? 'gray' : 'black',
            textDecoration: this.props.done ? 'line-through' : 'none'
        }
    }

    render(){

        return(
            <div className="posicion" style={this.StyleCompleted()}>     onClick={() => this.tick()}
                this.props.id -
                this.props.title - 
                this.props.description
                <input id="checkbox" type="checkbox" />
                <button style={btnDelete} >x</button>
            </div>)
    }
}

Task.propTypes={
    task: PropTypes.object.isRequired
}

export default Task;